import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import swal from 'sweetalert2';

import { MeasurementAndDosageSizesService } from '../../services/MeasurementAndDosageSizesService';
import { element } from 'protractor';


@Component({
  selector: 'add-manufacturing',
  templateUrl: './add-manufacturing.component.html',
  styleUrls: [
    './add-manufacturing.component.scss'
  ],
  providers: [MeasurementAndDosageSizesService]
})
export class AddManufacturingComponent implements OnInit {
  
manufacture_name:any;
manfactureUpdate:any = {};

@ViewChild(DatatableComponent) manufacturingTable: DatatableComponent;

rowsFilter = [];
tempFilter = [];

manuFacturingArray:any = [];

  constructor(public _measurementAndDosageSizesService: MeasurementAndDosageSizesService) {
  }
  ngOnInit() {
    this.getManufacturing();
  }

  addManufacture(){
    if(this.manufacture_name != null && this.manufacture_name != undefined && this.manufacture_name != '')
    {
    var body = {
      Name: this.manufacture_name
    }
    this._measurementAndDosageSizesService.addManufacturing(body).subscribe(response=>{

      console.log(response);

      if (response.code == 200) {
        this.manuFacturingArray.push(response.data);
        this.rowsFilter = this.manuFacturingArray;
        this.tempFilter = this.manuFacturingArray;
      
      }
      else {
        console.log('Error adding Manufacturer Name.');
      }
    }, err => {
      console.log('Error adding Manufacturer Name:', err);
    });
  }
} 

getManufacturing(){
  this._measurementAndDosageSizesService.getAllManufactures().subscribe(response => {

      if(response.code ==200){
        this.manuFacturingArray = response.data;
        this.rowsFilter = this.manuFacturingArray;
        this.tempFilter = this.manuFacturingArray;
        // console.log(this.manuFacturingArray);
      }

  })

}

view(id){
 var element = this.manuFacturingArray.find(abc => abc.Id == id);

 this.manfactureUpdate = element;
}

update(){
  var body = {
    Id: this.manfactureUpdate.Id,
    Name: this.manfactureUpdate.Name
  }

  this._measurementAndDosageSizesService.updateManufacture(body).subscribe(response => {
    console.log(response);
  });
  
}

Deletion(id){


var self = this;
//
swal({
  title: 'Are you sure?',
  text: 'You wont be able to revert',
  type: 'warning',
  showCancelButton: true,
  confirmButtonColor: '#3085d6',
  cancelButtonColor: '#d33',
  confirmButtonText: 'Yes, delete it!'
}).then(function (result) {
  if (result.value == true) {
      //Manfacture Delete
      self._measurementAndDosageSizesService.DeleteManufacture(id).subscribe(response => {
        var index_number = self.manuFacturingArray.findIndex(x => x.Id == id);
        console.log(index_number);
        self.manuFacturingArray.splice(index_number, 1);
        self.rowsFilter = self.manuFacturingArray;
        self.tempFilter = self.manuFacturingArray;
        swal(
          'Deleted!',
          'Manfacture has been deleted.',
          'success'
        );
      });
  }
  else {
    // alert('No deletion');
  }
}).catch(swal.noop);

}


updateFilter(event) {
  const val = event.target.value.toLowerCase();

  // filter our data
  const temp = this.tempFilter.filter(function (d) {
    return d.Name.toLowerCase().indexOf(val) !== -1 || !val;
  });

  // update the rows
  this.rowsFilter = temp;
  // Whenever the filter changes, always go back to the first page
  this.manufacturingTable.offset = 0;
}

onActivate(event) { }


}



